.main_background {
	position: relative;
	max-width:1920px;
	height: 740px;
	&_tutorial{
		display: none;
	}
	&_lock img {
		max-width: 180px; 
		position: absolute; 
		max-height: 220px; 
		left: 13%;
		top: 325px;
		z-index: 1;
	}
	&_page img {
		position: absolute;
		max-width: 550px;
		max-height: 480px;
		left: 20%;
		top: 160px;
	}
	&_line_violet img {
		position: absolute;
		max-width: 70%;
		max-height: 283px; 
		left: 0px; 
		top: 400px;
		pointer-events: none;
	}
	&_book img {
		position: absolute;
		max-width: 230px;
		max-height: 220px;
		left: 38%;
		top: 490px;
		z-index: 1;
	}
	&_line_gray img {
		position: absolute;
		max-width: 70%;
		max-height: 519px;
		left: 0px;
		top: 210px;
		pointer-events: none;
	}
	&_cookie img {
		position: absolute;
		max-width: 150px;
		max-height: 100px;
		left: 23%;
		top: 460px;
	}
	&_paperplane img{
		position: absolute;
		max-width: 85px;
		max-height: 95px;
		left: 65%;
		top: 370px;
	}
}
.main_swiper{
	&_solution{
		display: none;
	}
}
.main_tutorial-streamline{
  display: none;
}
.main_cardPricing{
	margin-top: 100px;
	height: 525px;
	display: block;
	position: relative;
	.main_cardPricing_detail{
		padding: 0 37px;
		.p_large,
		.h2_large,
		.h4_large{
			display: none;
		}
		.p_medium{
			font-family: 'Poppins';
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 150%;
			text-align: center;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			color: #B6BFD9;
			margin-bottom: 8px;
		}
		.h2_medium{
			font-family: 'Poppins';
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 120%;
			text-align: center;
			color: #1D212A;
			margin-bottom: 8px;
		}

		.h4_medium{
			font-family: 'Open Sans';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 150%;
			text-align: center;
			color: #565A65;
