.main_tutorial_small{
	position: relative;
	&_background{
		left: 0%;
		bottom: 30%;
		position: absolute;
		width: 101%;
		@include respond-to(medium) {
			left: 19%;
			bottom: 13%;
			width: 63%;
		}
		img{
			width: 100%;
		}
	}
	h5{
		font-family: $Poppins;
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		text-align: center;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: #B6BFD9;
		margin-bottom: 8px;
		@include respond-to(medium) {
			font-size: 18px;
			margin-bottom: 16px;
		}
	}
	h1{
		font-family: $Poppins;
		font-style: normal;
		font-weight: 300;
		font-size: 32px;
		line-height: 120%;
		text-align: center;
		color: $text-color;
		width: 95%;
		margin: 0 auto 32px;
		@include respond-to(medium) {
			font-size: 64px;
			margin-bottom: 48px;
			width: 62%
		}
	}
	&_button{
		display: flex;
		gap: 10px;
		align-items: center;
		justify-content: center;
		margin-top: 3%;
		&_generate{
				@include generate;
				border:none;
				font-family: $Poppins;
		}
		&_learn{
				@include learn;
				border: none;
				font-family: $Poppins;
		}
	}
}
.main_streamline_small{
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 50px;
	&_background img{
		position: relative; 
		width: 100%;
		left: 10px; 
		top: 50px; 
	}
	&_description{
		padding-top: 10%;
		padding-left: 5%;
		&_text_gradient{
			position: absolute;
			width: 254px;
			max-height: 288px;
			top: 500px;
			background-repeat: no-repeat;
		}
		&_name{
			font-family: $Poppins;
			font-style: normal;
			font-weight: 500;
			font-size: 18px;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			color: #B6BFD9;
		}
		&_title{
			font-family: $Poppins;
			font-style: normal;
			font-weight: 300;
			font-size: 24px;
			color: #1D212A;
		}
		&_more{
			padding-top: 1%;
			font-family: $Open_Sans;
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			color: #565A65;
		}
		&_detail{
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			padding: 0px;
			gap: 16px;
			padding-top: 4%;
			padding-left: 5%;
			&_firstPoint, &_secondPoint, &_thirdPoint, &_fourthPoint, &_fifthPoint{
				display: flex;
				flex-direction: row;
				align-items: flex-start;
				gap:14px;
			}
			&_style{
				font-family: $Open_Sans;
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
				color: #565A65;
			}
		}
		&_button{
			display: flex;
			justify-content: center;
			gap: 5%;
			padding-top: 3%;
			&_generate{
				@include generate;
			}
			&_learn{
				@include learn;
			}
		}
	}
}

.main_img_small{
	background-color: #FFFFFF;
	border-radius: 8px;
	display: grid;
	grid-template-columns: auto auto;
	column-gap: 18%;
	row-gap: 30px;
	justify-items: center;
	justify-content: center;
	padding: 2% 5%;
	width: 80%;
	margin: 0 auto;
	margin-top: 2%;
	position: relative;
	z-index: 2;
	top: 50px;
	div {
		max-width: 129px;
		p{
			font-family: $Open_Sans;
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			color: #1D212A;
		}
	}
}
