.main_background {
	@include respond-to(large){
		&_tutorial{
			display: block;
			max-width: 437px;
			position: relative;
			top:150px;
			left:25%;
			z-index: 1;
			&_text{
				font-family:$Poppins;
				font-style: normal;
				font-weight: 500;
				font-size: 18px;
				letter-spacing: 0.1em;
				text-transform: uppercase;
				color: #B6BFD9;
				text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
			}		
			&_aggrements{
				width: calc(89%);
				font-family: $Poppins;
				font-style: normal;
				font-weight: 300;
				font-size: 48px;
				line-height: 120%;
				color: #1D212A;
			}		
			&_aggrements_policy{
				font-family: $Poppins;
				font-style: normal;
				font-weight: 300;
				font-size: 20px;
				color: #646464; 
				margin-bottom: 41px;
				&_color{
					color:#32D989;
				}
				&_bolder {
					font-weight: bold;
				}
			}
			&_button{
				display: flex;
				gap:3%;
				&_generate{
					display: inline-block;
					padding: 12px 32px;
					border-radius: 8px;
					border: none;
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 150%;
					color:white;
					position: relative;
					transition: 0.5s;
					margin-bottom: 5px;
					background-color: #8770FF;
					cursor: pointer;
					&:hover{
						background: #8770FF;
						color: white;
						transition: 0.5s;
						&::before{
							display: none;
						}
						&::after{
							display: none;
						}
					}
				}
				&_learn{
					display: block;
					padding: 12px 45px 12px 32px;
					background: none;
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 150%;
					color: #1D212A;
					position: relative;
					transition: 0.5s;
					border: none;
					cursor: pointer;
					&::after{
						content: '';
						width: 2px;
						height: 8px;
						transform: rotate(45deg);
						background: #32D989;
						position: absolute;
						top: 23px;
						left: 135px;
					}
					&::before{
						content: '';
						width: 2px;
						height: 8px;
						transform: rotate(-45deg);
						background: #32D989;
						position: absolute;
						top: 19px;
						left: 135px;
					}
				}
			}
		}
		&_lock img {
			max-width: 150px; 
			max-height: 330px; 
			left: 2%;
		}
		&_page img {
			position: absolute;
			max-width: 700px;
			max-height: 600px;
			left: 60%;
			top: 160px;
		}
		&_line_violet img {
			position: absolute;
			max-width: 90%;
			max-height: 283px; 
			left: 0px; 
			top: 400px;
		}
		&_book img {
			position: absolute;
			max-width: 430px;
			max-height: 320px;
			left: 55%;
			top: 490px;
			z-index: 1;
		}
		&_line_gray img {
			position: absolute;
			max-width: 90%;
			max-height: 519px;
			left: 0px;
			top: 210px;
		}
		&_cookie img {
			position: absolute;
			max-width: 300px;
			max-height: 250px;
			left: 23%;
			top: 540px;
		}
		&_paperplane img{
			position: absolute;
			max-width: 85px;
			max-height: 95px;
			left: 85%;
			top: 370px;
		}
	}
}
