.main_safeguards_small{
	@include respond-to(large){
  	display: none;
	}
}
.main_img_large{
	min-height: 380px;
	display: grid;
	grid-template-columns: 30%  30%;
	background-color: #FFFFFF;
	border-radius: 8px;
	justify-content: center;
	column-gap: 18%;
	row-gap: 32px;
	width: 92%;
	margin: 0 auto;
	padding: 3% 6%;
	position: relative;
	bottom: -40px;
	@include respond-to (medium) {
		grid-template-columns: auto auto auto auto;
		column-gap: 14%;
		row-gap: 48px;
		width: 89%;
		padding: 3% 6%;
		z-index: 2;
	}
	@include respond-to (large) {
		grid-template-columns: auto auto auto auto; 
		column-gap: 18%;
		row-gap: 20%;
		width: 60%;
		padding: 3% 6%;
		z-index: 2;
	}
	div{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 8px;
	}
	img{
		height: 46px;
		width: 46px;
	}
	p{
		font-family: $Open_Sans;
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 150%;
		color: $text-color;
		text-align: center;
	}
}
.main_swiperPost{
	@include respond-to(large){
		left:10%;
	}
}
.main_post{
	@include respond-to(large){
		&_container{
			padding: 0px;
			gap: 30px;
			margin-bottom: 60px;
		}
	}
}

